#outer-screen{
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 10%;
    top: 24%;
    transition-duration: .1s;
    backdrop-filter: blur(5px);
    box-shadow: 1px 1px 10px black;
    border-radius: 10px;
    width: 74vw;
    height: 65vh;
    background: none;
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: -1;
    visibility: hidden;
}

.useritemContainer{
    background-color: whitesmoke;
    display: flex;
    flex-direction: row;
    padding: .2%;
    min-width: 97%;
    height: 12vh;
    margin: 1%;
    border-radius: 10px;
    box-shadow: .1px 8px 20px gray;
}

.cart-item-pic{
    display: block;
    max-width: 10%;
    max-height: 100%;
    border-radius: 10px;
}

.labels{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    text-align: center;
    bottom: 25%;
    padding: 1%;
    background-color: white;
    box-shadow: .1px .1px 8px black;
    margin-left: 1.5%;
    width: 25%;
    /* font-size: 1.3em; */
    font-size: clamp(.7rem, 2vw, 1.3rem);
    font-weight: lighter;
    font-family: 'Times New Roman', Times, serif;
    height: 40%;
    border-radius: 10px;
}

.value-display{
    color: lime;
    margin-left: 3%;
    text-shadow: .1px .1px 0px black;
}


.delete-button{
    position: relative;
    top: 17%;
    color: white;
    text-shadow: 1px 0px 0px black;
    margin-left: 1.4%;
    min-width: 7%;
    height: 70%;
    border-radius: 10px;
    box-shadow: .2px 3px 2px grey;
    border: .1px solid red;
    background-image: linear-gradient(0deg, red, white);
    cursor: pointer;
}
.delete-button:hover{transform: scale(1.1);}


#close-button{
    position: relative;
    left: 5%;
    border-radius: 10px;
    font-size: 1.2em;
    font-size: clamp(.7rem, 2vw, 1.2rem);
    transition-duration: .2s;
    background-color: whitesmoke;
    border: none;
    box-shadow: 1px 9px 17px gray;
    margin-top: 1%;
    margin-bottom: 1%;
    cursor: pointer;
    width: 90%;
}

#close-button:hover{transform: scale(1.1);}
