
:root{
    --nav-bg-img: linear-gradient(0deg, white, blue);
   }
   
   #header{
       position: relative;
       display: flex;
       flex-flow: row;
       width: 99vw;
       min-height: 18vh;
       margin: 0;
       padding: 0;
       box-shadow: .8px 7px 9px grey;
       background-color: whitesmoke;
       border-radius: 5px;
       perspective: 250px;
       z-index: 1;
   }
 

   #bg{
       background-color: whitesmoke;
       left: 8%;
       top: -45%;
       position: absolute;
       transition-duration: .5s;
       z-index: -1;
       width: 9%;
       height: 95%;
       border-radius: 46%;
   }
   
   #navBar{
       position: relative;
       display: flex;
       flex-flow: row;
       transition-duration: 1s;
       width: 38%;
       height: 55%;
       margin: .8%;
       padding: 0;
       border-radius: 10px;
       background-image: var(--nav-bg-img);
       z-index: -2;
   }
   
   .nav-button{
   position: relative;
   margin-left: 8%;
   /* font-size: 1em; */
   font-size: clamp(.6rem, 2vw, 1rem);
   width: 9%;
   height: 96%;
   transition-duration: .5;
   border: none;
   background: none;
   border-radius: 50%;
   cursor: pointer;
   }
   
   .svg-nav-icons{
       width: 80%;
       height: 80%;
   }
   .component-container{
       position: absolute;
       top: 10%;
       left: 10%;
       text-align: center;
       width: 80%;
       height: 80%;
       transition-duration: .5s;
       background: none;
       border-radius: 50%; 
   }
   .text{
       position: absolute;
       font-size: 1.3em;
       font-weight: bold;
       font-family: 'serif';
       top: 48%;
       left: 5%;
       transition-duration: .2s;
       color: white;
       text-shadow: 3px 0px 0px black;
       visibility: hidden;
       z-index: -1;  
   }
   
   #cart-price{
       position: relative;
       left: 32%;
       display: flex;
       flex-flow: row;
       width: 27%;
       height: 70%;
       margin-top: .3%;
   }
   #price{
       position: relative;
       top: 16%;
       display: flex;
       flex-flow: row;
       background-image: var(--nav-bg-img);
       transition-duration: .1s;
       width: 54%;
       height: 99%;
       border-radius: 10px;
       overflow-x: hidden;
   }
   
   #price:hover{
       transform: scale(1.02);
       box-shadow: .1px -6px 10px gray;
   }
   #price label {
       /* font-size: 1.4em; */
       font-size: clamp(.8rem, 2vw, 1.4rem);
       width: max-content;
       color: white;
       text-shadow: 2px 0px 0px black;
   }
   #cart{
       display: flex;
       flex-flow: row;
       position: relative;
       left: 2%;
       top: 14%;
       transition-duration: .1s;
       background-image: var(--nav-bg-img);
       width: 47%;
       height: 65%;
       border-radius: 10px;
   }
   
   #cart:hover{box-shadow: -.1px -6px 8px grey;}
   
    #cart div h6{
       position: relative;
       left: 15%;
       bottom: 167%;
       /* font-size: 1.1em; */
       font-size: clamp(.9rem, 2vw, 1.1rem);
       color: white;
       font-weight: lighter;
    }
   
    #cart label{
       position: relative;
       color: white;
       text-shadow: 1px 0px 0px black;
       top: 20%;
       /* font-size: 1.2em; */
       font-size: clamp(.7rem, 2vw, 1.2rem);
    }
    
    #svg-button{
       position: relative;
       left: 10%;
       transition-duration: .1s;
       background: none;
       border: none;
       width: 34%;
       cursor: pointer;
       border-radius: 10px;
    }
    #svg-button:hover{
      
       transform: scale(1.08);
       background-image: linear-gradient(0deg, aqua, lightgreen);
       box-shadow: .1px 18px 25px lime;
       border: 3px solid lightgreen;
       z-index: 1;
   
    }
    #cart-svg{
       position: relative;
       left: 1%;
       top: -6%;
    }
   
   #purchase-button{
       position: relative;
       font-size: clamp(.7rem, 2vw, 1rem);
       font-weight: bold;
       font-family: Arial, Helvetica, sans-serif;
       transition-duration: .1s;
       top: 65%;
       left: 20%;
       width: 10%;
       height: 28%;
       border-radius: 10px;
       text-shadow: 1px 0px 0px black;
       color: white;
       background-image: var(--nav-bg-img);
       border: none;
       cursor: pointer;
   }
   
   #purchase-button:hover{
       transform: scale(1.08);
       box-shadow: -.1px -8px 5px gray;
   }
   